
    
    .nft {
        text-align: center;
      user-select: none;
      width: 100%;
      /* margin: 5rem auto; */
      border: 1px solid rgba(255, 255, 255, 0.1333333333);
      /* background-color: #282c34;  */
      background-color: rgba(255, 255, 255, 0.2);
      /* background: linear-gradient(0deg, rgb(40, 44, 52) 0%, rgba(17, 0, 32, 0.5) 100%);  */
      /* box-shadow: 0 7px 20px 5px rgba(0, 0, 0, 0.5333333333); */
      border-radius: 0.7rem;
      backdrop-filter: blur(10px);
      -webkit-backdrop-filter: blur(10px);
      overflow: hidden;
      transition: 0.5s all;
    }
    .nft hr {
      width: 100%;
      border: none;
      border-bottom: 1px solid rgba(136, 136, 136, 0.3333333333);
      margin-top: 0;
    }
    .nft ins {
      text-decoration: none;
    }
    .nft .main {
      display: flex;
      flex-direction: column;
      width: 100%;
      padding: 1rem;
    }
    .nft .main .tokenImage {
      border-radius: 0.5rem;
      max-width: 100%;
      height: 250px;
      object-fit: cover;
    }
    
    .tokenImage {
     width: 65px;
     height: 65px !important;
    }
    
    .image {
      display: flex;
      justify-content: center;
    }
    
    
    
    .description {
      text-align: center;
    }
    
    .nft .main .tokenInfo {
      display: flex;
      justify-content: space-between;
      align-items: center;
    }
    .nft .main .tokenInfo .price {
      display: flex;
      align-items: center;
      color: #ee83e5;
      font-weight: 700;
    }
    .nft .main .tokenInfo .price ins {
      margin-left: -0.3rem;
      margin-right: 0.5rem;
    }
    .nft .main .tokenInfo .duration {
      display: flex;
      align-items: center;
      color: #a89ec9;
      margin-right: 0.2rem;
    }
    .nft .main .tokenInfo .duration ins {
      margin: 0.5rem;
      margin-bottom: 0.4rem;
    }
    .nft .main .creator {
      display: flex;
      align-items: center;
      margin-top: 0.2rem;
      margin-bottom: -0.3rem;
    }
    .nft .main .creator ins {
      color: #a89ec9;
      text-decoration: none;
    }
    .nft .main .creator .wrapper {
      display: flex;
      align-items: center;
      /* border: 1px solid rgba(255, 255, 255, 0.1333333333); */
      padding: 0.3rem;
      margin: 0;
      margin-right: 0.5rem;
      border-radius: 100%;
      box-shadow: inset 0 0 0 4px rgba(0, 0, 0, 0.6666666667);
    }
    
    .wrapper {
      display: none;
    }
    
    .nft .main .creator .wrapper img {
      border-radius: 100%;
      /* border: 1px solid rgba(255, 255, 255, 0.1333333333); */
      width: 2rem;
      height: 2rem;
      object-fit: cover;
      margin: 0;
    }
    .nft ::before {
      position: fixed;
      content: "";
      box-shadow: 0 0 100px 40px rgba(255, 255, 255, 0.031372549);
      top: -10%;
      left: -100%;
      transform: rotate(-45deg);
      height: 60rem;
      transition: 0.7s all;
    }
    .nft:hover {
      border: 1px solid rgba(255, 255, 255, 0.2666666667);
      /* box-shadow: 0 7px 50px 10px rgba(0, 0, 0, 0.6666666667); */
      transform: scale(1.015);
      /* filter: brightness(1.3); */
    }
    .nft:hover ::before {
    
      /* filter: brightness(0.5); */
      top: -150%;
      left: 200%;
    }
    
    .bg {
      position: fixed;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
    }
    .bg h1 {
      font-size: 20rem;
      filter: opacity(0.5);
    }
    
    /*# sourceMappingURL=output.css.map */
    